<template>
  <div class="main-container">
    <div class="post-header">
      <h2 class="post-title">{{ post.title }}</h2>
      <p class="post-author">{{ post.userId }} 님</p>
      <div class="post-actions">
        <button class="edit-button" @click="showEditConfirmation = true">수정</button>
        <button class="delete-button" @click="showDeleteConfirmation = true">삭제</button>
      </div>
    </div>

    <div class="post-images-section" v-if="post.images && post.images.length">
      <div v-for="(image, index) in post.images" :key="index" class="post-image" :style="{ backgroundImage: 'url(' + getFirstImage(image) + ')' }"></div>
    </div>

    <div class="post-content-section">
      <div class="post-content">{{ post.body }}</div>
    </div>

    <div class="comments-container">
      <comments-section :article="article"></comments-section>
    </div>

    <transition name="fade">
      <div v-if="showDeleteConfirmation" class="delete-confirmation">
        <p>게시글을 삭제하시겠습니까?</p>
        <div class="button-container">
          <button class="delete-yes" @click="confirmDelete">확인</button>
          <button class="delete-no" @click="showDeleteConfirmation = false">취소</button>
        </div>
      </div>
    </transition>

    <transition name="fade">
      <div v-if="showEditConfirmation" class="delete-confirmation">
        <p>게시글을 수정하시겠습니까?</p>
        <div class="button-container">
          <button class="delete-yes" @click="confirmEdit">확인</button>
          <button class="delete-no" @click="showEditConfirmation = false">취소</button>
        </div>
      </div>
    </transition>
  </div>
</template>


<script>
import axios from "axios";
import { mapGetters } from "vuex";
import CommentsSection from './CommentsSection.vue';

export default {
  name: "PostDetails",
  components: {
    CommentsSection
  },
  updated(){
    console.log("updated")
  },
  mounted(){
    console.log("mounted")
    this.fetchPostData();
    this.emitter.emit("updateButtons", {
      menuButton: false,
      searchButton: false,
      backButton: true,
    });
  },
  created() {
    this.fetchPostData();
    this.emitter.emit("updateButtons", {
      menuButton: false,
      searchButton: false,
      backButton: true,
    });
  },
  // props: {
  //   post: {
  //     type: Object,
  //     required: false,
  //     default: () => ({}),
  //   },
  // },
  data() {
    return {
      post: [],
      article: { id: 1 },
      comments: [],
      newComment: "",
      showDeleteConfirmation: false,
      showEditConfirmation: false,
      editCommentData: {
        id: null,
        nickname: "",
        content: ""
      },
    };
  },
  computed: {
    ...mapGetters(["getReforme"]),
  },
  methods: {
    fetchPostData() {
      //this.param = this.$route.params.id;
    console.log("업데이트 됨?")
      const path = this.$route.path;
      let url;
      if (path.includes("reforme")) {
        url = `/reforme/board/${this.$route.params.id}`;
      } else if (path.includes("reforyou")) {
        url = `/reforyou/board/${this.$route.params.id}`;
      }

    axios
        .get(url)
        .then((response) => {
          if (response.data.statusCode === 200) {
            this.post = response.data.data;
            //this.comments = response.data.data.comments;
          } else {
            alert("수정해야될 데이터 불러오기 실패");
          }
        })
        .catch((error) => {
          alert("수정해야될 데이터 불러오기 실패: " + error.message);
        });
    },
    getFirstImage(image) {
      const imageUrl = image ? `${image.imagePath}` : "";
      return imageUrl;
    },
    editPost() {
      this.showEditConfirmation = true;
    },
    confirmEdit() {
      this.showEditConfirmation = false;
      const path = this.$route.path;
      //let reformeValue;
      if (path.includes("/reforyou")) {
        this.$router.push({
        name: "reforyou_writePage",
        params: { id: this.post.boardId },
      });
      } else if (path.includes("/reforme")) {
        this.$router.push({
        name: "reforme_writePage",
        params: { id: this.post.boardId },
      });
      }
      //this.$store.dispatch("updateReforme", reformeValue);
      // this.$router.push({
      //   name: "reforme_writePage",
      //   params: { id: this.post.boardId },
      // });
    },
    deletePost() {
      this.showDeleteConfirmation = true;
    },
    confirmDelete() {
      const path = this.$route.path;
      let reformeValue;
      if (path.includes("/reforyou")) {
        reformeValue = false;
      } else if (path.includes("/reforme")) {
        reformeValue = true;
      }
      console.log(reformeValue)
      const url = reformeValue //this.getReforme
        ? `/reforme/board/${this.post.boardId}`
        : `/reforyou/board/${this.post.boardId}`;
      axios
        .delete(url)
        .then((response) => {
          if (response.data.statusCode === 200) {
            alert("게시글 삭제 성공");
            const redirectPage = this.getReforme
              ? "/reforme_page"
              : "/reforyou_page";
            this.$router.push(redirectPage);
          } else {
            alert("게시글 삭제 실패");
          }
        })
        .catch((error) => {
          alert("게시글 삭제 실패: " + error.message);
        });
      this.showDeleteConfirmation = false;
    },
    editComment(comment) {
      this.editCommentData = { ...comment };
    },
    updateComment() {
      const url = this.getReforme
        ? `/reforme/board/${this.post.boardId}/comment?id=${this.editCommentData.id}`
        : `/reforyou/board/${this.post.boardId}/comment?id=${this.editCommentData.id}`;
      axios
        .patch(url, this.editCommentData, {
          headers: {
            "Content-Type": "application/json",
          },
        })
        .then((response) => {
          if (response.data.statusCode === 200) {
            alert("댓글 수정 성공");
            this.comments = this.comments.map((comment) =>
              comment.id === this.editCommentData.id
                ? { ...comment, content: this.editCommentData.content }
                : comment
            );
          } else {
            alert("댓글 수정 실패");
          }
        })
        .catch((error) => {
          alert("댓글 수정 실패: " + error.message);
        });
    },
    deleteComment(commentId) {
      const url = this.getReforme
        ? `/reforme/board/${this.post.boardId}/comment?id=${commentId}`
        : `/reforyou/board/${this.post.boardId}/comment?id=${commentId}`;
      axios
        .delete(url)
        .then((response) => {
          if (response.data.statusCode === 200) {
            alert("댓글 삭제 성공");
            this.comments = this.comments.filter(
              (comment) => comment.id !== commentId
            );
          } else {
            alert("댓글 삭제 실패");
          }
        })
        .catch((error) => {
          alert("댓글 삭제 실패: " + error.message);
        });
    },
  },
};
</script>



